import { Box, Typography, IconButton, Container } from "@mui/material";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import TelegramIcon from "@mui/icons-material/Telegram";

export default function Footer() {
  return (
    <div className="Footer">
      <Box
        component="footer"
        sx={{
          backgroundColor: "#f5f5f5",
          borderTop: "1px solid #ddd",
          py: 3,
          mt: 5,
        }}
      >
        <Container
          maxWidth="md"
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            flexWrap: "wrap",
          }}
        >
          <Typography
            variant="body1"
            fontWeight="bold"
            sx={{ mb: { xs: 1, md: 0 } }}
          >
            Oasis Cars © {new Date().getFullYear()}
          </Typography>

          <Box>
            <IconButton
              component="a"
              href="https://wa.me/998901234567"
              target="_blank"
              rel="noopener"
              aria-label="WhatsApp"
            >
              <WhatsAppIcon color="success" />
            </IconButton>

            <IconButton
              component="a"
              href="https://t.me/oasiscars"
              target="_blank"
              rel="noopener"
              aria-label="Telegram"
            >
              <TelegramIcon sx={{ color: "#229ED9" }} />
            </IconButton>
          </Box>
        </Container>
      </Box>
    </div>
  );
}
