import '../style.css';

const categories = [
  "Все", "Без депозита", "Эконом", "Стандарт",
  "Бизнес", "Люкс", "Спорт", "Электро", "7 мест"
];

export default function FilterBar({ active, setActive, sort, setSort }) {
  const toggleCategory = (cat) => {
    setActive(cat === "Все" ? '' : cat);
  };

  const toggleSort = (type) => {
    setSort(prev => prev === type ? '' : type);
  };

  return (
    <div>
      <div className="filter-bar">
        {categories.map((cat) => (
          <button
            key={cat}
            onClick={() => toggleCategory(cat)}
            className={`filter-button ${
              (active === cat || (cat === "Все" && !active)) ? 'active' : ''
            }`}
          >
            {cat}
          </button>
        ))}
      </div>

      <div className="sort-bar">
        <button
          onClick={() => toggleSort("asc")}
          className={`sort-button ${sort === "asc" ? "active" : ""}`}
        >
          Самые дешевые
        </button>
        <button
          onClick={() => toggleSort("desc")}
          className={`sort-button ${sort === "desc" ? "active" : ""}`}
        >
          Самые дорогие
        </button>
      </div>
    </div>
  );
}
