import React, { useState } from 'react';
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  MenuItem,
  Menu,
  Select,
  FormControl,
  InputLabel,
  Box,
  Drawer,
  List,
  ListItem,
  ListItemText,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link } from 'react-router-dom';
import logo from './logo.png';
import '../style.css';

export default function Navbar({ user, lang, setLang }) {
  const [mobileOpen, setMobileOpen] = useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ width: 250 }}>
      <List>
        <ListItem button component={Link} to="/">
          <ListItemText primary="Главная" />
        </ListItem>
        <ListItem button component={Link} to="/about">
          <ListItemText primary="О нас" />
        </ListItem>
        {user ? (
          <ListItem button component={Link} to="/account">
            <ListItemText primary="Личный кабинет" />
          </ListItem>
        ) : (
          <ListItem button component={Link} to="/login">
            <ListItemText primary="Вход" />
          </ListItem>
        )}
      </List>
    </Box>
  );

  return (
    <AppBar position="static" sx={{ backgroundColor: 'white', boxShadow: 1 }}>
      <Toolbar>
        <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
          <Link to="/" className="logo-link">
            <img src={logo} alt="logo" className="logo-img" />
            <Typography variant="h6" className="logo-text">Oasis Cars</Typography>
          </Link>
        </Box>

        <Box sx={{ display: { xs: 'none', md: 'flex' }, alignItems: 'center', gap: 2 }}>
          <Link to="/about" className="nav-about-link">О нас</Link>

          <FormControl size="small">
            <InputLabel id="lang-label">Язык</InputLabel>
            <Select
              labelId="lang-label"
              value={lang}
              onChange={(e) => setLang(e.target.value)}
              label="Язык"
              sx={{ minWidth: 100 }}
            >
              <MenuItem value="ru">Русский</MenuItem>
              <MenuItem value="en">English</MenuItem>
            </Select>
          </FormControl>

          {user ? (
            <Link to="/account" className="nav-link">Личный кабинет</Link>
          ) : (
            <Link to="/login" className="nav-link">Вход</Link>
          )}
        </Box>

        <IconButton
          edge="end"
          color="inherit"
          aria-label="menu"
          onClick={handleDrawerToggle}
          sx={{ display: { md: 'none' }, color: 'black' }}
        >
          <MenuIcon />
        </IconButton>
      </Toolbar>

      <Drawer
        anchor="right"
        open={mobileOpen}
        onClose={handleDrawerToggle}
      >
        {drawer}
      </Drawer>
    </AppBar>
  );
}
