import '../style.css';
import { useNavigate } from 'react-router-dom';

export default function CarCard({ car, lang }) {
  const navigate = useNavigate();
  const name = lang === 'ru' ? car.name_ru : car.name_en;
  const description = lang === 'ru' ? car.description_ru : car.description_en;

  const imageUrl = Array.isArray(car.images) && car.images.length > 0
    ? car.images[0].image
    : 'https://via.placeholder.com/400x250?text=No+Image';

  return (
    <div className="car-card clickable" onClick={() => navigate(`/car/${car.id}`)}>
      <img src={imageUrl} alt={name} className="car-image" />
      <div className="car-content">
        <span className="car-tag">{car.category}</span>
        <h3 className="car-title">{name}</h3>
        <p className="car-price">{car.price_per_day} AED / день</p>
        <p className="car-desc">{description}</p>
      </div>
    </div>
  );
}
