import { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import axios from 'axios';
import {
  Container, Typography, Box, Button, Grid, IconButton
} from '@mui/material';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import { useSwipeable } from 'react-swipeable';
import '../style.css';

export default function CarDetail({ lang }) {
  const { id } = useParams();
  const [car, setCar] = useState(null);
  const [index, setIndex] = useState(0);

  useEffect(() => {
    axios.get(`https://innermentorlab.com/api/cars/${id}/`)
      .then(res => {
        setCar(res.data);
        setIndex(0);
      })
      .catch(err => {
        console.error("Машина не найдена:", err);
        setCar(null);
      });
  }, [id]);

  const nextImage = () => {
    if (car?.images?.length > 0) {
      setIndex((prev) => (prev + 1) % car.images.length);
    }
  };

  const prevImage = () => {
    if (car?.images?.length > 0) {
      setIndex((prev) => (prev - 1 + car.images.length) % car.images.length);
    }
  };

  const handlers = useSwipeable({
    onSwipedLeft: nextImage,
    onSwipedRight: prevImage,
    preventScrollOnSwipe: true,
    trackMouse: true
  });

  if (!car) {
    return <p className="loading-text">Машина не найдена</p>;
  }

  const name = lang === 'ru' ? car.name_ru : car.name_en;
  const desc = lang === 'ru' ? car.description_ru : car.description_en;
  const images = car.images || [];
  const whatsapp = '998901234567';

  return (
    <Container maxWidth="md" sx={{ pt: 3, pb: 8 }}>
      <Link to="/" className="back-link" style={{ marginBottom: 16, display: 'inline-block' }}>
        ← Назад
      </Link>

      {images.length > 0 ? (
        <Box position="relative" {...handlers} sx={{ borderRadius: 3, overflow: 'hidden' }}>
          <Box sx={{ position: 'relative', height: 300, overflow: 'hidden', borderRadius: 3, mb: 1 }}>
            {images.map((img, i) => (
              <Box
                key={i}
                component="img"
                src={img.image}
                alt=""
                sx={{
                  position: 'absolute',
                  width: '100%',
                  height: '100%',
                  objectFit: 'cover',
                  top: 0,
                  left: 0,
                  opacity: i === index ? 1 : 0,
                  transition: 'opacity 0.5s ease-in-out'
                }}
              />
            ))}
          </Box>

          {images.length > 1 && (
            <>
              <IconButton onClick={prevImage} sx={{ position: 'absolute', top: '50%', left: 8, transform: 'translateY(-50%)', background: '#fff' }}>
                <ArrowBackIosNewIcon fontSize="small" />
              </IconButton>
              <IconButton onClick={nextImage} sx={{ position: 'absolute', top: '50%', right: 8, transform: 'translateY(-50%)', background: '#fff' }}>
                <ArrowForwardIosIcon fontSize="small" />
              </IconButton>
            </>
          )}
        </Box>
      ) : (
        <Box sx={{ height: 300, display: 'flex', alignItems: 'center', justifyContent: 'center', border: '1px dashed grey', borderRadius: 3, mb: 1 }}>
          <Typography>Нет изображений</Typography>
        </Box>
      )}

      <Box mt={2}>
        <Typography variant="subtitle2" color="text.secondary" gutterBottom>
          {car.is_automatic ? 'Автомат' : 'Механика'} · {car.category}
        </Typography>

        <Typography variant="h6" fontWeight="bold">{name}</Typography>

        <Typography variant="body2" color="text.secondary" sx={{ mt: 1, whiteSpace: 'pre-line' }}>
          {desc}
        </Typography>

        <Grid container spacing={2} mt={2}>
          <Grid item xs={6}>
            <Typography variant="body2"><b>Цена:</b> {car.price_per_day} AED/день</Typography>
            <Typography variant="body2"><b>Пробег:</b> {car.mileage} км</Typography>
            <Typography variant="body2"><b>Примеры цен:</b> {car.pricing_examples}</Typography>
          </Grid>
          <Grid item xs={6}>
            <Typography variant="body2"><b>Категория:</b> {car.category}</Typography>
            <Typography variant="body2"><b>Трансмиссия:</b> {car.is_automatic ? 'Автомат' : 'Механика'}</Typography>
          </Grid>
        </Grid>
      </Box>

      <Box position="fixed" bottom={16} left={0} right={0} display="flex" justifyContent="center">
        <Button
          variant="contained"
          size="large"
          href={`https://wa.me/${whatsapp}`}
          target="_blank"
          startIcon={<WhatsAppIcon />}
          sx={{
            backgroundColor: '#ff3d00',
            borderRadius: '30px',
            fontWeight: 'bold',
            px: 4,
            py: 1.5,
            '&:hover': { backgroundColor: '#e53900' }
          }}
        >
          Связаться
        </Button>
      </Box>
    </Container>
  );
}
